import React from 'react';
import Group from 'modules/Group';
import History from 'modules/History';
import Statistic from 'modules/Statistic';

export const disciplineRoutes = [
  {
    path: '/kicker',
    name: '',
    component: <Statistic />
  },
  {
    path: '/pong',
    name: ' ',
    component: <Statistic />
  },
  {
    path: '/somedis',
    name: '',
    component: <Statistic />
  }
];

export const routes = [
  {
    path: '/',
    name: '',
    component: <Group />
  },
  {
    path: '/:id',
    name: ' ',
    component: <History />
  },
  ...disciplineRoutes
];
